}
.withTable .table-container {
  font-size: 1.2rem;
}
.withTable .table-container table {
  margin-bottom: 1rem;
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 2px 10px rgba(44, 52, 60, 0.6);
}
.withTable .table-container th,
.withTable .table-container td {
  padding: 1rem;
